<template>
  <div>
    <div class="header_intergral">
      <van-nav-bar title="商品订单" left-arrow @click-left="onClickLeft">
        <van-icon name="ellipsis" slot="right" />
      </van-nav-bar>
    </div>
    <div class="integral_tochange">
      <van-tabs v-model="active">
        <van-tab title="全部订单">
          <!--  -->
          <div v-for="(item,index) in newarr" :key="index" class="card_top">
            <div class="card_time">订单提交时间：{{time[index]|guolue}}</div>
            <div class="card_number">订单编号：{{number[index]}}</div>
            <van-card
              :title="item.title"
              :desc="item.desc"
              :num="item.num"
              :price="formatPrice(item.price)"
              :thumb="item.thumb"
            >
              <div slot="footer">
                <div>共{{item.num}}件商品&nbsp;&nbsp;&nbsp;&nbsp;合计：￥{{item.num*formatPrice(item.price)}}</div>
                <van-button size="small">延迟收货</van-button>
                <van-button size="small">查看物流</van-button>
                <van-button size="small">确认收货</van-button>
              </div>
            </van-card>
          </div>

          <!--  -->

          <noorder v-show="isflag"></noorder>
          <more></more>
        </van-tab>
        <van-tab title="待付款">
          <noorder></noorder>
          <more></more>
        </van-tab>
        <van-tab title="待收货">
          <noorder></noorder>
          <more></more>
        </van-tab>
        <van-tab title="待评价">
          <noorder></noorder>
          <more></more>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import more from "@/components/public/more.vue";
import noorder from "@/components/public/noorder.vue";
export default {
  data() {
    return {
      active: 0,
      newarr: [],
      isflag: true,
      time: this.$store.state.integral.time,
      number: this.$store.state.integral.number
    };
  },
  components: {
    more,
    noorder
  },
  methods: {
    onClickLeft() {
      this.$router.push("/wd");
    },
    formatPrice(price) {
      return (price / 100).toFixed(2);
    }
  },
  created() {
    window.console.log(Math.floor(Math.floor(Math.random() * 1000000000)));
    this.active = this.$store.state.integral.isactive;
    window.console.log(this.active);
    if (this.$store.state.Cart.dingdan.length != 0) {
      this.isflag = false;
    }
    //
    this.newarr = this.$store.state.Cart.dingdan;
  },
  filters: {
    //过滤
    guolue: function(data) {
      var dd = data;
      var y = dd.getFullYear();
      var m = dd.getMonth() + 1;
      var d = dd.getDate();
      var h = dd.getHours();
      var f = dd.getMinutes();
      var mm = dd.getSeconds();
      mm = mm < 10 ? "0" + String(mm) : mm;
      h = h < 10 ? "0" + String(h) : h;
      f = f < 10 ? "0" + String(f) : f;
      return `${y}-${m}-${d} ${h}:${f}:${mm}`; //模板字符串
    }
  }
};
</script>

<style lang="scss">
.header_intergral {
  .van-icon {
    color: black;
    font: 20px/1 "vant-icon";
  }
  .van-nav-bar .van-icon {
    color: black;
  }
}
.card_top {
  margin-top: 10px;
  .van-card:not(:first-child) {
    margin: 0;
  }
  .card_time {
    box-sizing: border-box;
    padding: 0px 16px;
    width: 100%;
    background-color: #fafafa;
  }
  .card_number {
    box-sizing: border-box;
    padding: 0px 16px;
    width: 100%;
    background-color: #fafafa;
  }
  .van-button {
    border-radius: 5px;
  }
}
</style>